body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}



*{
  margin: 0;
  padding : 0;
}

.game{
  max-width: 100%;
  height: auto;
}

.game-board button{
  border: 0;
  width: 6.25%;
}

img{
  max-width:100%;
  height:auto;
}

.test{
  position: absolute;
  background-color: black;
}

.game-board{
  width: 60%;
  max-width:60%;
  float: left;
}

.sub{
  width: 40%;
  max-width: 40%;
  /* height: 800px; */
  float: left;
  background-color: aquamarine;
}

.sub img{
  max-width: 100%;
  height: auto;
}

.sub-kawa img{
  max-width: 5%;
  height: auto;
}

.sub button{
  width:5%;
  height:auto;
}

@media screen and (max-width:640px){
  .game{
    height: 100%;
  }
  .game-board{
    width: 100%;
    max-width:100%;
    float: none;
  }

  .sub{
    width: 100%;
    float: none;
    max-width: 100%;
  }
}
